import React, { useState } from "react";
import { Link } from "react-router-dom";
import "../asset/css/MobileTab.css";
import i_close from "../asset/img/btn_close.svg";
import i_cart from "../asset/img/i_cart.svg";

function MobileTab({ setNavOpen, navOpen, tabs, setTabs }) {
  //제휴및 협업
  const goWithWho = () => {
    const goWith = () => {
      const header = document.querySelector("header");
      const brand = document.querySelector(".main_section_withwho");
      const headerHeight = header.offsetHeight;
      const brandHeight = brand.offsetTop;
      const goWhere = brandHeight - headerHeight - 60;
      window.scrollTo({ top: goWhere, left: 0, behavior: "auto" });
    };
    setTimeout(goWith, 50);
  };
  //히스토리로
  const goHistory = () => {
    const goH = () => {
      const header = document.querySelector("header");
      const his = document.querySelector(".history_title");
      const headerHeight = header.offsetHeight;
      const hisHeight = his.offsetTop;
      const goWhere = hisHeight - headerHeight - 30;
      window.scrollTo({ top: goWhere, left: 0, behavior: "auto" });
    };
    setTimeout(goH, 50);
  };
  //그로잉샵
  const goShop = () => {
    window.location.href = "https://smartstore.naver.com/growingmom/";
  };
  //뭐 누를때마다 스크롤을 맨 위로 보내기 함수 생성
  const setScrollToTop = () => {
    window.scrollTo({ top: 0, left: 0, behavior: "auto" });
  };

  return (
    <div className={navOpen ? "mobile_tab" : "mobile_tab mobile_tab_close"}>
      <div className="mobile_tab_inner">
        <div className="mobile_tab_box1">
          <div className="btn_close" onClick={() => setNavOpen(false)}>
            <img alt="" src={i_close} />
          </div>
        </div>
        <div className="mobile_tab_box2">
          <ul onClick={setScrollToTop}>
            <li onClick={() => setNavOpen(false)}>
              <Link to="/brand">그로잉맘 소개</Link>
              <ul className="sub_menu">
                <li>
                  <Link to="/brand">그로잉맘 소개</Link>
                </li>
                <li>
                  <Link to="/brand" onClick={goHistory}>
                    히스토리
                  </Link>
                </li>
              </ul>
            </li>
            <li onClick={() => setNavOpen(false)}>
              <Link to="/service" onClick={() => setTabs(1)}>
                그로잉맘 서비스
              </Link>
              <ul className="sub_menu">
                <li onClick={() => setNavOpen(false)}>
                  <Link to="/service" onClick={() => setTabs(1)}>
                    기질 분석
                  </Link>
                </li>
                <li onClick={() => setNavOpen(false)}>
                  <Link to="/service" onClick={() => setTabs(2)}>
                    육아 상담
                  </Link>
                </li>
                <li onClick={() => setNavOpen(false)}>
                  <Link to="/service" onClick={() => setTabs(3)}>
                    큐레이션
                  </Link>
                </li>
                <li onClick={() => setNavOpen(false)}>
                  <Link to="/service" onClick={() => setTabs(4)}>
                    부모 교육
                  </Link>
                </li>
              </ul>
            </li>
            <li onClick={() => setNavOpen(false)}>
              <Link to="/faq">자주 묻는 질문</Link>
            </li>
            <li onClick={() => setNavOpen(false)}>
              <Link to="/" onClick={goWithWho}>
                제휴 및 협업
              </Link>
            </li>
            {/* <li onClick={() => setNavOpen(false)}>
              <Link to="#" onClick={goShop}>
                그로잉샵
              </Link>
            </li> */}
          </ul>
        </div>
      </div>
      <div className="yellow_shop" onClick={goShop}>
        <div className="yellow_shop_wrap">
          <img alt="" src={i_cart} />
          <p>그로잉샵</p>
        </div>
      </div>
    </div>
  );
}

export default MobileTab;
