import React, { useState, useEffect } from "react";
import { Link, useHistory } from "react-router-dom";
import "../asset/css/Header.css";
import logo_top from "../asset/img/logo/logo_top.svg";
import logo_bot from "../asset/img/logo/logo_bot.svg";
import i_hamburger from "../asset/img/i_hamburger.svg";
import i_close from "../asset/img/btn_close.svg";

function Header({ setNavOpen, navOpen, tabs, setTabs }) {
  let history = useHistory();
  const goShop = () => {
    window.location.href = "https://smartstore.naver.com/growingmom/";
  };

  const [hover, setHover] = useState({
    li1: false,
    li2: false,
    li3: false,
    li4: false,
    li5: false,
  });
  const [active, setActive] = useState({
    at1: false,
    at2: false,
    at3: false,
    at4: false,
    at5: false,
  });

  //제휴및 협업
  const goWithWho = () => {
    const goWith = () => {
      const header = document.querySelector("header");
      const brand = document.querySelector(".main_section_withwho");
      const headerHeight = header.offsetHeight;
      const brandHeight = brand.offsetTop;
      const goWhere = brandHeight - headerHeight;
      window.scrollTo({ top: goWhere, left: 0, behavior: "auto" });
    };
    setTimeout(goWith, 50);
  };
  //뭐 누를때마다 스크롤을 맨 위로 보내기 함수 생성
  const setScrollToTop = () => {
    window.scrollTo({ top: 0, left: 0, behavior: "auto" });
  };
  //스크롤
  const [scrollState, setScrollState] = useState(0);
  useEffect(() => {
    window.addEventListener("scroll", onScroll);
    return () => window.removeEventListener("scroll", onScroll);
  }, []);
  const onScroll = (e) => {
    const scrollTop =
      (document.documentElement && document.documentElement.scrollTop) ||
      document.body.scrollTop;
    // const scrollTop = e.target.scrollingElement.scrollTop;
    // this.setState({ scrollTop });
    setScrollState(scrollTop);
  };
  

  return (
    <div className="header_wrap">
      <header
        className={
          window.location.pathname === "/faq" || scrollState > 250
            ? "header pc_header header_change"
            : "header pc_header"
        }
      >
        <div className="header_inner">
          <h1
            className="top_logo"
            onClick={() =>
              setActive({
                at1: false,
                at2: false,
                at3: false,
                at4: false,
                at5: false,
              })
            }
          >
            <Link to="/" onClick={setScrollToTop}>
              <img alt="" src={logo_top} />
            </Link>
          </h1>
          <nav className="gnb">
            <ul onClick={setScrollToTop}>
              <li
                // onMouseOver={() => {
                //   setHover({ ...hover, li1: true });
                // }}
                // onMouseOut={() => {
                //   setHover({ ...hover, li1: false });
                // }}
                onClick={() => {
                  setActive({ at1: true });
                }}
                className={
                  hover.li1 ||
                  active.at1 ||
                  window.location.pathname === "/service"
                    ? "hover"
                    : null
                }
              >
                <Link to="/service" onClick={() => setTabs(1)}>
                  그로잉맘의 서비스
                </Link>
              </li>
              <li
                // onMouseOver={() => {
                //   setHover({ ...hover, li2: true });
                // }}
                // onMouseOut={() => {
                //   setHover({ ...hover, li2: false });
                // }}
                onClick={() => {
                  setActive({ at2: true });
                }}
                className={
                  hover.li2 ||
                  active.at2 ||
                  window.location.pathname === "/brand"
                    ? "hover"
                    : null
                }
              >
                <Link to="/brand">그로잉맘 소개</Link>
              </li>
              <li
                // onMouseOver={() => {
                //   setHover({ ...hover, li3: true });
                // }}
                // onMouseOut={() => {
                //   setHover({ ...hover, li3: false });
                // }}
                onClick={() => {
                  setActive({ at3: true });
                }}
                className={
                  hover.li3 || active.at3 || window.location.pathname === "/faq"
                    ? "hover"
                    : null
                }
              >
                <Link to="/faq">자주 묻는 질문</Link>
              </li>
              <li
                // onMouseOver={() => {
                //   setHover({ ...hover, li4: true });
                // }}
                // onMouseOut={() => {
                //   setHover({ ...hover, li4: false });
                // }}
                onClick={() => {
                  setActive({ at4: true });
                }}
                className={
                  hover.li4 //||
                    ? //(window.location.pathname === "/" && scrollState > 8500)
                      "hover"
                    : null
                }
              >
                <Link to="/" onClick={goWithWho}>
                  제휴 및 협업
                </Link>
              </li>
              <li
                // onMouseOver={() => {
                //   setHover({ ...hover, li5: true });
                // }}
                // onMouseOut={() => {
                //   setHover({ ...hover, li5: false });
                // }}
                className={hover.li5 ? "hover" : null}
              >
                <Link to="#" onClick={goShop}>
                  그로잉샵
                </Link>
              </li>
            </ul>
          </nav>
        </div>
      </header>
      <header
        className={
          window.location.pathname === "/faq" ||
          window.location.pathname === "/brand" ||
          window.location.pathname === "/service/1" ||
          scrollState > 250 ||
          navOpen
            ? "header mo_header"
            : "header mo_header header_change"
        }
      >
        <div className="header_inner">
          <h1 className="top_logo" onClick={setScrollToTop}>
            <Link to="/" onClick={() => setNavOpen(false)}>
              <img alt="" src={logo_top} />
            </Link>
          </h1>
          <div className="open_tab" onClick={() => setNavOpen(!navOpen)}>
            {navOpen ? (
              <img alt="" src={i_close} className="i_close" />
            ) : (
              <img alt="" src={i_hamburger} className="i_hamburger" />
            )}
          </div>
        </div>
      </header>
    </div>
  );
}

export default Header;
