// Slick
import React, { Component } from "react";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import "../asset/css/Slider.css";
// img
import i_btnLeft from "../asset/img/main/ic_left_arrow.svg";
import i_btnRight from "../asset/img/main/ic_right_arrow.svg";
import m_partner_logos1 from "../asset/img/with/m_partner_logos1.png";
import m_partner_logos2 from "../asset/img/with/m_partner_logos2.png";
import m_partner_logos3 from "../asset/img/with/m_partner_logos3.png";


//버튼디자인
function SampleNextArrow(props) {
  const { className, onClick, style } = props;
  return (
    <div
      className="arrow btn_right"
      onClick={onClick}
      style={{
        ...style,
        display: "block",
        width: "3.1rem",
        height: "5.7rem",
        cursor: "pointer",
        position: "absolute",
        top: "17rem",
        right: "-13.4rem",
        effect: "coverflow",
      }}
    >
      <img src={i_btnRight} alt="" />
    </div>
  );
}

function SamplePrevArrow(props) {
  const { className, onClick, style } = props;
  return (
    <div
      className="arrow btn_left"
      onClick={onClick}
      style={{
        ...style,
        display: "block",
        width: "3.1rem",
        height: "5.7rem",
        cursor: "pointer",
        position: "absolute",
        top: "17rem",
        left: "-13.4rem",
      }}
    >
      <img src={i_btnLeft} alt="" />
    </div>
  );
}

export default class SliderPcWithWho extends Component {
  render() {
    const settings = {
      infinite: true,
      speed: 500,
      slidesToShow: 1,
      slidesToScroll: 1,
      autoplay: true,
      autoplaySpeed: 4000,
      arrows: false,
      dots: true,
      pauseOnHover: false,
    };
    return (
      <div className="slider_with_who mo_on">
        <Slider {...settings}>
          <div className="with_who_page1">
            <div className="with_page_wrap">
              <img alt="" src={m_partner_logos1} />
            </div>
          </div>
          <div className="with_who_page2">
            <div className="with_page_wrap">
            <img alt="" src={m_partner_logos2} />
            </div>
          </div>
          <div className="with_who_page3">
            <div className="with_page_wrap">
            <img alt="" src={m_partner_logos3} />
            </div>
          </div>
        </Slider>
      </div>
    );
  }
}
