import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
//img
import i_download from "../asset/img/ic_download.svg";
import i_kakao from "../asset/img/i_kakao.svg";

function Download() {
   //스크롤
   const [scrollState, setScrollState] = useState(0);
   useEffect(() => {
     window.addEventListener("scroll", onScroll);
     return () => window.removeEventListener("scroll", onScroll);
   }, []);
   const onScroll = (e) => {
     const scrollTop =
       (document.documentElement && document.documentElement.scrollTop) ||
       document.body.scrollTop;
     // const scrollTop = e.target.scrollingElement.scrollTop;
     // this.setState({ scrollTop });
     setScrollState(scrollTop);
   };

  return (
    <div className="download">
      <div className="channel_talk mo_on mo_channel_talk">
        <img alt="" src={i_kakao} />
      </div>
      <div className="download_wrap">
        <div className="i_download">
          <img alt="" src={i_download} />
        </div>
        <p>앱 다운받기</p>
      </div>
    </div>
  );
}

export default Download;
